<template>
  <div class="container">
    <HeadTop :title="$t('index.上幣記錄')">
      <template v-slot:rightText>
        <span @click="$router.push({name:'RecordUploadToken'})">{{$t('index.記錄')}}</span>
      </template>
    </HeadTop>
    <div class="upload">
      <div class="info-wrap">
        <div class="key">{{$t('index.上幣須知：')}}</div>
        <div class="value">{{$t('index.當前僅支持BSC鏈代幣，上幣前請先前往SWAP創建該代幣與USDT的流動性，創建成功後，支付key即上幣成功，並開放該代幣的的質押挖礦',{key: applyFee + ' USDT'})}}</div>
      </div>
      <div class="form">
        <div class="li flex-between">
          <div class="label">{{$t('index.代幣名字')}}</div>
          <div class="input-wrap flex-middle flex-between">
            <van-field :clearable="true" v-model="name" :placeholder="$t('index.請輸入项目名称')">
            </van-field>
          </div>
        </div>
        <div class="li flex-between">
          <div class="label">{{$t('index.合約地址')}}</div>
          <div class="input-wrap flex-middle flex-between">
            <van-field :clearable="true" v-model="token" :placeholder="$t('index.請輸入代币合約地址')">
            </van-field>
          </div>
        </div>
        <!-- <div class="li flex-between border-1px-b">
          <div class="label">{{$t('index.代幣圖標')}}</div>
          <label class="label-wrap flex-middle flex-between">
            <img v-if="imgSrc" :src="imgSrc" alt="" width="80">
            <img v-else class="icon" src="@/assets/images/upload@2x.png" alt="" width="80">
            <AppUpload @imgSrc="imgSrcHandle" />
          </label>
        </div> -->
        <div class="li" style="display:block;">
          <div class="label">{{$t('index.項目詳情')}}</div>
          <div class="textarea-wrap flex-middle flex-between">
            <!-- <textarea rows="1" v-model="desc" :placeholder="$t('index.請輸入不多於key字',{key:100})"></textarea> -->
            <van-field v-model="desc" rows="1"  type="textarea" maxlength="100" :placeholder="$t('index.請輸入不多於key字',{key:100})" show-word-limit />
          </div>
        </div>
      </div>
      <div class="btn-wrap">
        <van-button v-if="Number(usdtAllowance)>0" class="btn" :loading="btnLoading" size="large" :loading-text="$t('index.提交') + '...'" @click="apply">{{$t('index.提交')}}</van-button>
        <van-button v-else class="btn" :loading="btnLoading" size="large" :loading-text="$t('index.授權') + '...'" @click="approveUsdt">{{$t('index.授權key',{key:'USDT'})}}</van-button>
      </div>
      <van-dialog v-model:show="showDialog">
        <img class="img" src="@/assets/images/dialog-succes@2x.png" alt="">
        <div class="text">{{$t('index.上幣成功')}}</div>
      </van-dialog>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import UvStakeAbi from "@/utils/UvStakeAbi";
import { Toast } from 'vant';
import { MaxUint256 } from "@ethersproject/constants";
import BigNumber from 'bignumber.js';
import AppUpload from '@/components/AppUpload'
import routerPath from "@/utils/routerPath";
export default {
  components: {
    HeadTop,
    AppUpload
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      imgSrc: '',
      name: '',
      token: '',
      desc: '',
      isPreview: false,
      showDialog: false,
      UvStakeContract: null,
      UsdtContract: null,
      RouterPathContract: null,
      usdtAllowance: 0,
      applyFee: '0',
      btnLoading: false,
      initContract: () => {
        data.UvStakeContract = new data.web3.eth.Contract(UvStakeAbi, config.uvStakeAddress)
        data.RouterPathContract = new data.web3.eth.Contract(routerPath, config.routerPath)
        data.UsdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.initData()
      },
      initData: () => {
        data.getUsdtAllowance()
        data.getApplyFee()
      },
      imgSrcHandle: (img) => {
        data.imgSrc = img
      },
      getApplyFee: async () => {
        let decimals = await data.UsdtContract.methods.decimals().call();
        let usdtBalance = await data.UsdtContract.methods.balanceOf(data.address).call()
        let fee = await data.UvStakeContract.methods.applyFee().call()
        data.applyFee = new BigNumber(fee).dividedBy(10 ** decimals).toFixed(4)
        data.balanceOf = new BigNumber(usdtBalance).dividedBy(10 ** decimals).toFixed(4)
      },
      getUsdtAllowance: async () => {
        let result = await data.UsdtContract.methods.allowance(data.address, config.uvStakeAddress).call();
        data.usdtAllowance = result;
      },
      approveUsdt: async () => {
        data.btnLoading = true
        try {
          await data.UsdtContract.methods.approve(config.uvStakeAddress, MaxUint256).send({ from: data.address });
        data.getUsdtAllowance()
         data.btnLoading = false
        } catch (error) {
          data.btnLoading = false
        }
       
      },
      apply: async () => {
        if (!data.name) {
          Toast(data.$t('index.請輸入項目名稱'))
          return
        }
        if (!data.token) {
          Toast(data.$t('index.請輸入合約地址'))
          return
        }
        // if (!data.imgSrc) {
        //   Toast(data.$t('index.請上傳圖標'))
        //   return
        // }
        if (!data.desc) {
          Toast(data.$t('index.請輸入項目描述'))
          return
        }
        let amount = await data.RouterPathContract.methods.getBestPathAmount(config.usdtAddress, data.token, 1+ '0'.repeat(18)).call()
        if(Number(amount) <= 0) {
          Toast(data.$t('index.請先添加交易對'))
          return
        }
        data.btnLoading = true
        try {
          await data.UvStakeContract.methods.applyToken(data.name, data.token, data.desc).send({ from: data.address });
          data.showDialog = true;
          data.btnLoading = false
        } catch (error) {
          Toast(data.$t('index.代幣錯誤'))
        }

      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
