<template>
  <div>
    <!-- capture="camera" -->
    <input @change="getImages" :multiple="multiple" type="file" class="btn" ref="fileInput" accept="image/*">
  </div>
</template>
<script>
import { Toast } from 'vant';
import { reactive, toRefs, getCurrentInstance } from "vue";
import { useI18n } from "vue-i18n";
export default {
  props: {
    type: {
      default: 'icon'
    },
    multiple: {
      default: false
    }
  },
  setup (props, { emit }) {
    const { t } = useI18n()
    const ctx = getCurrentInstance()
    const data = reactive({
      $t: t,
      limitData: [
        {
          type: 'icon',
          size: 1 * 1024 * 150
        },
      ],
      limitSize: 0,
      temp: 0,
      type: props.type,
      multiple: props.multiple
    })
    const getImages = (e) => {
      let files = e.target.files
      if (!(files && files.length)) {
        return
      }
      Toast.loading({
        duration: 0
      });
      for (let file of files) {
        compressor(file)
      }
    }
    const compressor = (file) => {
      let size = file.size
      data.limitData.map(item => {
        item.type === data.type ? data.limitSize = item.size : ''
      })
      if (size > data.limitSize) {
        data.limitSize / 1024 / 1024 >= 1 ? data.temp = data.limitSize / 1024 / 1024 + 'MB' : data.temp = data.limitSize / 1024 + 'KB'
        Toast(data.$t('index.圖片大小不能超過', { key: data.temp }))
        ctx.refs.fileInput.value = ''
        Toast.clear()
        return false
      }
      getImgToBase64(file, function (result) {
        Toast.clear()
        emit('imgSrc', result)
      });
    }
    const getImgToBase64 = (file, cb) => {
      let reader = new FileReader();
      reader.onload = function (evt) {
        let base64 = evt.target.result;
        cb(base64);
      };
      reader.readAsDataURL(file);
    }
    const refData = toRefs(data);
    return {
      ...refData,
      getImages,
      compressor,
      getImgToBase64
    };
  }
}
</script>
<style lang="less" scoped>
.btn {
  display: block;
  width: 1px;
  height: 1px;
  opacity: 0;
}
</style>
